:root {
  --c-white: #ffffff;
  --c-black: #000000;
  --c-dark: #333;
  --c-grey: #888888;
  --c-primary: #4054b2;
  --c-blue: #4c74d3;
  --admin-bar-height: 0;
}

/* ====== GLOBAL STYLES  ====== */
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  direction: rtl;
  text-align: right;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  line-height: 1.6;
  color: #929292;
  unicode-bidi: embed;
  background-color: #fff;
}

@media (max-width: 991px) {
  body {
    font-size: 16px;
  }
}

::selection {
  color: #fff;
  background: #333;
  text-shadow: none;
}

a {
  transition: 0.2s all;
  text-decoration: none;
  color: var(--c-dark);
  font-weight: 500;
  font-size: 15px;
}

a:hover {
  color: var(--c-primary);
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  margin: 0 0 20px;
  color: #333;
}

@media (max-width: 991px) {
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 15px;
  }
}

h1 {
  font-size: 35px;
  line-height: 1.3;
}

@media (max-width: 991px) {
  h1 {
    font-size: 30px;
  }
}

h2 {
  font-size: 30px;
  line-height: 1.35;
}

@media (max-width: 991px) {
  h2 {
    font-size: 25px;
  }
}

h3 {
  font-size: 24px;
  line-height: 1.5;
}

@media (max-width: 991px) {
  h3 {
    font-size: 21px;
  }
}

h4 {
  font-size: 20px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h4 {
    font-size: 18px;
  }
}

h5 {
  font-size: 18px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h5 {
    font-size: 16px;
  }
}

h6 {
  font-size: 16px;
  line-height: 1.6;
}

@media (max-width: 991px) {
  h6 {
    font-size: 14px;
  }
}

p,
ul,
ol {
  margin: 0 0 20px;
}

@media (max-width: 991px) {
  p,
  ul,
  ol {
    margin-bottom: 15px;
  }
}

ol,
ul {
  padding-inline-start: 1.2em;
}

blockquote {
  font-style: italic;
  margin: 0 0 20px;
}

@media (max-width: 991px) {
  blockquote {
    margin-bottom: 15px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.t-center {
  text-align: center;
}

.t-right {
  text-align: right;
}

.t-left {
  text-align: left;
}

/* =WordPress Core
-------------------------------------------------------------- */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  /* Image does not overflow the content area */
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

.gallery-caption {
  font-size: 0.9em;
}

.bypostauthor {
  font-style: italic;
}

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

#wpadminbar {
  position: fixed;
}

/* ====== PAGE TEMPLATE STYLES  ====== */
.hr-page {
  min-height: calc(100vh - var(--admin-bar-height));
  display: flex;
  flex-direction: column;
}

.hr-section {
  padding-top: calc(30px + 2.5vw);
}

.hr-section:last-of-type {
  padding-bottom: calc(30px + 2.5vw);
}

.hr-section__title {
  margin-bottom: calc(20px + 1.5vw);
  font-size: 30px;
  text-align: center;
}

@media (max-width: 991px) {
  .hr-section__title {
    font-size: 24px;
  }
}

.hr-header {
  position: sticky;
  background-color: var(--c-blue);
  width: 100%;
  left: 0;
  top: var(--admin-bar-height);
  z-index: 100;
}

.hr-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.hr-header__bar-wrap {
  font-size: 14px;
}

.hr-header__bar-wrap a {
  color: #fff;
  font-size: inherit;
}

.hr-header__bar-wrap a:hover {
  opacity: 0.7;
}

.hr-header__bar-wrap a i {
  margin-left: 0.33em;
}

@media (min-width: 1025px) {
  .hr-header .menu-primary-menu-container {
    padding-top: 15px;
    margin-top: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  .hr-header .menu {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    justify-content: space-between;
  }
  .hr-header .menu i {
    display: none;
  }
  .hr-header .menu .menu-item-has-children {
    position: relative;
  }
  .hr-header .menu .menu-item-has-children:hover > .sub-menu {
    opacity: 1;
    visibility: visible;
  }
  .hr-header .menu > li > a {
    display: block;
    width: 100%;
    color: var(--c-white);
    font-size: 18px;
    position: relative;
  }
  .hr-header .menu > li > a::after {
    display: inline-block;
    content: '';
    position: absolute;
    left: 0;
    bottom: -15px;
    width: 100%;
    height: 4px;
    background-color: currentColor;
    opacity: 0;
    transition: 0.35s opacity;
  }
  .hr-header .menu > li:hover > a::after {
    opacity: 1;
  }
  .hr-header .sub-menu {
    position: absolute;
    background-color: var(--c-white);
    list-style: none;
    margin: 0;
    top: calc(100% + 15px);
    min-width: 180px;
    padding: 5px 0;
    opacity: 0;
    visibility: hidden;
    transition: 0.35s;
  }
  .hr-header .sub-menu a {
    font-size: 18px;
    display: block;
    padding: 5px 15px;
    line-height: 1.2;
  }
  .hr-header .sub-menu > li:hover > a {
    color: var(--c-white);
    background-color: var(--c-blue);
  }
  .hr-header .sub-menu .sub-menu {
    top: 0;
    right: 100%;
  }
  .hr-header__nav {
    max-width: 75%;
    padding-right: 50px;
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .hr-header__bar-wrap {
    display: flex;
    justify-content: space-between;
    order: -1;
  }
  .hr-header__logo img {
    max-height: 80px;
    width: auto;
  }
  .hr-header__logo__burger {
    display: none;
  }
}

@media (max-width: 1024px) {
  .hr-header .container {
    max-width: none;
  }
  .hr-header .menu-primary-menu-container ul {
    list-style: none;
    padding-right: 0;
  }
  .hr-header .menu-primary-menu-container .menu {
    margin-bottom: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }
  .hr-header .menu-primary-menu-container .menu > li > a {
    font-size: 18px;
  }
  .hr-header .menu-primary-menu-container .menu i {
    color: #fff;
    position: absolute;
    left: 0;
    top: 0;
    height: 46px;
    width: 46px;
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
  }
  .hr-header .menu-primary-menu-container .menu i::before {
    display: block;
    transition: 0.35s;
  }
  .hr-header .menu-primary-menu-container .menu li {
    position: relative;
  }
  .hr-header .menu-primary-menu-container .menu li.is-opened > i::before {
    transform: rotate(-90deg);
  }
  .hr-header .menu-primary-menu-container .menu .menu-item-has-children > a {
    padding-left: 50px;
  }
  .hr-header .menu-primary-menu-container .menu a {
    color: var(--c-white);
    font-size: 16px;
    padding: 10px 30px;
    display: block;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    line-height: 24px;
  }
  .hr-header .menu-primary-menu-container .menu a:hover {
    opacity: 0.7;
  }
  .hr-header .menu-primary-menu-container .sub-menu {
    display: none;
  }
  .hr-header .menu-primary-menu-container .sub-menu a {
    padding-right: 45px;
  }
  .hr-header .menu-primary-menu-container .sub-menu .sub-menu a {
    padding-right: 60px;
  }
  .hr-header__nav {
    position: fixed;
    right: 0;
    top: var(--admin-bar-height);
    transform: translateX(100%);
    height: calc(100vh - var(--admin-bar-height));
    background-color: var(--c-blue);
    width: 270px;
    padding: 50px 0 30px;
    overflow: auto;
    transition: 0.4s transform;
    display: block;
  }
  .is-opened .hr-header__nav {
    transform: translateX(0);
  }
  .hr-header__overlay {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    opacity: 0;
    visibility: hidden;
    transition: 0.4s opacity, 0.4s visibility;
  }
  .is-opened .hr-header__overlay {
    opacity: 1;
    visibility: visible;
  }
  .hr-header .menu-primary-menu-container {
    margin-top: 0;
    padding-top: 0;
    border-top: 0;
    margin-bottom: 50px;
  }
  .hr-header__bar-wrap {
    padding-right: 30px;
    padding-left: 30px;
  }
  .hr-header__bar-wrap > div {
    margin-top: 15px;
  }
  .hr-header__logo img {
    max-height: 50px;
  }
  .hr-header__burger {
    width: 40px;
    height: 28px;
    position: relative;
  }
  .hr-header__burger span {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 3px;
    background-color: var(--c-white);
    display: block;
    transition: 0.2s 0.2s background-color;
  }
  .hr-header__burger span::after, .hr-header__burger span::before {
    position: absolute;
    left: 0;
    width: 100%;
    display: block;
    content: '';
    background-color: var(--c-white);
    height: 3px;
  }
  .hr-header__burger span::before {
    top: -12px;
    transition: 0.2s 0.2s top, 0.2s transform;
  }
  .hr-header__burger span::after {
    bottom: -12px;
    transition: 0.2s 0.2s bottom, 0.2s transform;
  }
  .hr-header__burger.is-opened span {
    background-color: transparent;
    transition: 0.2s background-color;
  }
  .hr-header__burger.is-opened span::before {
    top: 0;
    transform: rotate(45deg);
    transition: 0.2s top, 0.2s 0.2s transform;
  }
  .hr-header__burger.is-opened span::after {
    bottom: 0;
    transform: rotate(-45deg);
    transition: 0.2s bottom, 0.2s 0.2s transform;
  }
}

@media (max-width: 475px) {
  .hr-header__nav {
    width: calc(100% - 70px);
    max-width: none;
